                            <div class="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuButton">
                                <div class="dropdown-item"><input type="checkbox" :id="'card_email'" @click="filter('card_email', 'card_email')"> Все</div>
                                <div v-for="(key, item) in card_email" :key="item">
