                    </tr>
                    <tr class="trstyle">
                        <th class="thead elmincentertop">
                            <a class="dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">2021</a>
                            <div class="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuButton">
                                <div class="dropdown-item"><input type="checkbox" :id="'need_2021'" @click="filter('need_2021', 'need_2021')"> Все</div>
                                <div v-for="(key, item) in need_2021" :key="item">
