                                </div>
                            </div>
                        </th>
                        <th class="thead elmincentertop">
                            <a class="dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">2022</a>
                            <div class="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuButton">
                                <div class="dropdown-item"><input type="checkbox" :id="'need_2022'" @click="filter('need_2022', 'need_2022')"> Все</div>
                                <div v-for="(key, item) in need_2022" :key="item">
                                    <div class="dropdown-item"><input type="checkbox" :id="'need_2022.' + item" @click="filter('need_2022', 'need_2022', item)" checked> {{item}}</div>
                                </div>
                            </div>
                        </th>
                        <th class="thead elmincentertop">
                            <a class="dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">2023</a>
                            <div class="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuButton">
                                <div class="dropdown-item"><input type="checkbox" :id="'need_2023'" @click="filter('need_2023', 'need_2023')"> Все</div>
                                <div v-for="(key, item) in need_2023" :key="item">
                                    <div class="dropdown-item"><input type="checkbox" :id="'need_2023.' + item" @click="filter('need_2023', 'need_2023', item)" checked> {{item}}</div>
                                </div>
                            </div>
                        </th>
                        <th class="thead elmincentertop">
                            <a class="dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Наличие<wbr> МТБ<wbr> для<wbr> подготовки</a>
                            <div class="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuButton">
                                <div class="dropdown-item"><input type="checkbox" :id="'mtb_for_prepare'" @click="filter('mtb_for_prepare', 'mtb_for_prepare')"> Все</div>
                                <div v-for="(key, item) in mtb_for_prepare" :key="item">
                                    <div class="dropdown-item"><input type="checkbox" :id="'mtb_for_prepare.' + item" @click="filter('mtb_for_prepare', 'mtb_for_prepare', item)" checked> {{item}}</div>
                                </div>
                            </div>
                        </th>
                        <th class="thead elmincentertop">
                            <a class="dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Сколько<wbr> стоит<wbr> создать<wbr> МТБ</a>
                            <div class="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuButton">
                                <div class="dropdown-item"><input type="checkbox" :id="'make_mtb'" @click="filter('make_mtb', 'make_mtb')"> Все</div>
                                <div v-for="(key, item) in make_mtb" :key="item">
                                    <div class="dropdown-item"><input type="checkbox" :id="'make_mtb.' + item" @click="filter('make_mtb', 'make_mtb', item)" checked> {{item}}</div>
                                </div>
                            </div>
                        </th>
                    </tr>
                    <tr v-for="item in rows1" :key="item" class="trstyle2" :id="item">
                        <td v-if="step1[item][16] == 15" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.1'" :value="step1[item][0]" @input="update_step1(item, 1), change_width()" style="width: 250px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px;"><textarea type="text" class="tinput" :id="item +'.2'" :value="step1[item][1]" @input="update_step1(item, 2), change_width()" style="width: 250px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.3'" :value="step1[item][2]" @input="update_step1(item, 3), change_width()" style="width: 250px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.4'" :value="step1[item][3]" @input="update_step1(item, 4), change_width()" style="width: 250px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.5'" :value="step1[item][4]" @input="update_step1(item, 5), change_width()" style="width: 100px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.6'" :value="step1[item][5]" @input="update_step1(item, 6), change_width()" style="width: 100px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.7'" :value="step1[item][6]" @input="update_step1(item, 7), change_width()" style="width: 100px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.8'" :value="step1[item][7]" @input="update_step1(item, 8), change_width()" style="width: 150px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.9'" :value="step1[item][8]" @input="update_step1(item, 9), change_width()" style="width: 150px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.10'" :value="step1[item][9]" @input="update_step1(item, 10), change_width()" style="width: 250px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.11'" :value="step1[item][10]" @input="update_step1(item, 11), change_width()" style="width: 250px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.12'" :value="step1[item][11]" @input="update_step1(item, 12), change_width()" style="width: 250px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.13'" :value="step1[item][12]" @input="update_step1(item, 13), change_width()" style="width: 250px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.14'" :value="step1[item][13]" @input="update_step1(item, 14), change_width()" style="width: 250px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.15'" :value="step1[item][14]" @input="update_step1(item, 15), change_width()" style="width: 250px;"></textarea></td>
                        <td v-if="step1[item][16] == 15" class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.16'" :value="step1[item][15]" @input="update_step1(item, 16), change_width()" style="width: 250px;"></textarea></td>
                        <td v-if="step1[item][16] == 15"><button class="delb" @click="del_row(item)"><i class="fas fa-times"></i></button></td>
                    </tr>
                </table>
            </div>
            <br>
            <br>
            <br>
            <br>
            <div class="row buttons">
                <div class="col-4">
                    <button class="btn btn-elegant swb" v-if="step != 0" @click="change_step('-')" style="width: 100%;">Назад</button>
                </div>
                <div class="col-4">
                    <button class="btn btn-almbb-success swb" @click="save()" style="width: 100%;">Сохранить</button>
                </div>
                <div class="col-4">
                    <button class="btn btn-blue swb" v-if="step != 1" style="width: 100%;" @click="change_step('+')">Далее</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// import 'table2excel';
// const Table2Excel = window.Table2Excel;
 
// const table2excel = new Table2Excel();
// import ExcellentExport from 'excellentexport';
import Swal from 'sweetalert2'
export default {
    name: 'EditCard',
    data(){
        return{
            step: 0,
            card_name: '',
            rows1: [],
            step1: [],
            companies: {},
            spheres: {},
            speciality_names: {},
            skill_levels: {},
            need_2021: {},
            need_2022: {},
            need_2023: {},
            y_n_expert_count: {},
            corp_prep_program: {},
            mtb_for_prepare: {},
            make_mtb: {},
            org_city: {},
            responsible_person: {},
            post: {},
            phone: {},
            card_email: {},
            email: this.$store.getters.email,
            SessionID: this.$store.getters.SessionID,
            id: this.$route.params.id,
        }
    },
    beforeMount(){
        if (this.email == undefined || this.email != 'admin') window.location.href = '/login'
        fetch(this.$store.state.serverIp+'/api/getData', {
            method: 'POST',
            body: JSON.stringify({email: this.email, sessionid: this.SessionID, tableid: this.id}),
            headers: {
                "Content-Type": "application/json"
            },
        })
        .then(response => {
            // console.log("res", response)
            return response.json()
        })
        .then(data => {
            this.card_name = data.data.data.card_name
            this.rows1 = data.data.data.rows1
            this.step1 = data.data.data.step1
            this.companies = data.data.data.companies
            this.spheres = data.data.data.spheres
            this.speciality_names = data.data.data.speciality_names
            this.skill_levels = data.data.data.skill_levels
            this.need_2021 = data.data.data.need_2021
            this.need_2022 = data.data.data.need_2022
            this.need_2023 = data.data.data.need_2023
            this.y_n_expert_count = data.data.data.y_n_expert_count
            this.corp_prep_program = data.data.data.corp_prep_program
            this.mtb_for_prepare = data.data.data.mtb_for_prepare
            this.make_mtb = data.data.data.make_mtb
            this.org_city = data.data.data.org_city
            this.responsible_person = data.data.data.responsible_person
            this.post = data.data.data.post
            this.phone = data.data.data.phone
            this.card_email = data.data.data.card_email
        })
        .catch(err => {
            console.log(err)
        })
    },
    beforeDestroy(){
        if(confirm("Сохранить изменения?")){
            let now = new Date()
            fetch(this.$store.state.serverIp+'/api/updateData', {
                method: 'POST',
                body: JSON.stringify({email: this.email, sessionid: this.SessionID, tableid: this.id, data: {
                    card_name: this.card_name,
                    rows1: this.rows1,
                    step1: this.step1,
                    companies: this.companies,
                    spheres: this.spheres,
                    speciality_names: this.speciality_names,
                    skill_levels: this.skill_levels,
                    need_2021: this.need_2021,
                    need_2022: this.need_2022,
                    need_2023: this.need_2023,
                    y_n_expert_count: this.y_n_expert_count,
                    corp_prep_program: this.corp_prep_program,
                    mtb_for_prepare: this.mtb_for_prepare,
                    make_mtb: this.make_mtb,
                    org_city: this.org_city,
                    responsible_person: this.responsible_person,
                    post: this.post,
                    phone: this.phone,
                    card_email: this.card_email,
                    update_user: this.email,
                    update_time: [now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate(), now.getUTCHours(), now.getUTCMinutes()]},
                }),
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                // console.log("res", response)
                return response.json()
            })
            .then(data => {
                if(data.data == '310'){
                    document.cookie = "email=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                    document.cookie = "SessionID=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                    window.location.reload()
                }
            })
            .catch(err => {
                console.log(err)
            })
        }
    },
    methods: {
        del_row(id){
            Swal.fire({
                text: 'Вы уверены, что хотите удалить эту строку?',
                showCancelButton: true,
                cancelButtonText: 'Отмена',
                preConfirm: () => {
                    this.rows1.splice(-1, 1)
                    this.step1.splice(id, 1)
                }
            })
        },
        save(){
            let now = new Date()
            fetch(this.$store.state.serverIp+'/api/updateData', {
                method: 'POST',
                body: JSON.stringify({email: this.email, sessionid: this.SessionID, tableid: this.id, data: {
                    card_name: this.card_name,
                    rows1: this.rows1,
                    step1: this.step1,
                    companies: this.companies,
                    spheres: this.spheres,
                    speciality_names: this.speciality_names,
                    skill_levels: this.skill_levels,
                    need_2021: this.need_2021,
                    need_2022: this.need_2022,
                    need_2023: this.need_2023,
                    y_n_expert_count: this.y_n_expert_count,
                    corp_prep_program: this.corp_prep_program,
                    mtb_for_prepare: this.mtb_for_prepare,
                    make_mtb: this.make_mtb,
                    org_city: this.org_city,
                    responsible_person: this.responsible_person,
                    post: this.post,
                    phone: this.phone,
                    card_email: this.card_email,
                    update_user: this.email,
                    update_time: [now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate(), now.getUTCHours(), now.getUTCMinutes()]},
                }),
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                // console.log("res", response)
                return response.json()
            })
            .then(data => {
                if(data.data == '310'){
                    document.cookie = "email=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                    document.cookie = "SessionID=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                    window.location.reload()
                }   
                else{
                    Swal.fire({
                        icon: 'success',
                        text: 'Файл успешно обновлен',
                        showConfirmButton: false,
                        timer: 1500,
                    });
                }
            })
            .catch(err => {
                console.log(err)
            })
        },
        change_width(item){
            let id
            if(item == undefined) id = event.target.id[0]
            else id = item
            let first = document.getElementById(id + '.1')
            let f = Math.max(Number(first.style.height.slice(0, -2)), first.scrollHeight)
            let second = document.getElementById(id + '.2')
            let s = Math.max(Number(second.style.height.slice(0, -2)), second.scrollHeight)
            let third = document.getElementById(id + '.3')
            let t = Math.max(Number(third.style.height.slice(0, -2)), third.scrollHeight)
            let fourth = document.getElementById(id + '.4')
            let fo = Math.max(Number(fourth.style.height.slice(0, -2)), fourth.scrollHeight)
            let fifth = document.getElementById(id + '.5')
            let fi = Math.max(Number(fifth.style.height.slice(0, -2)), fifth.scrollHeight)
            let sixth = document.getElementById(id + '.6')
            let si = Math.max(Number(sixth.style.height.slice(0, -2)), sixth.scrollHeight)
            let seventh = document.getElementById(id + '.7')
            let se = Math.max(Number(seventh.style.height.slice(0, -2)), seventh.scrollHeight)
            let eighth = document.getElementById(id + '.8')
            let ei = Math.max(Number(eighth.style.height.slice(0, -2)), eighth.scrollHeight)
            let ninth = document.getElementById(id + '.9')
            let ni = Math.max(Number(ninth.style.height.slice(0, -2)), ninth.scrollHeight)
            let tenth = document.getElementById(id + '.10')
            let te = Math.max(Number(tenth.style.height.slice(0, -2)), tenth.scrollHeight)
            let eleventh = document.getElementById(id + '.11')
            let el = Math.max(Number(eleventh.style.height.slice(0, -2)), eleventh.scrollHeight)
            let twelfth = document.getElementById(id + '.12')
            let tw = Math.max(Number(twelfth.style.height.slice(0, -2)), twelfth.scrollHeight)
            let thirteen = document.getElementById(id + '.13')
            let th = Math.max(Number(thirteen.style.height.slice(0, -2)), thirteen.scrollHeight)
            let fourteen = document.getElementById(id + '.14')
            let four = Math.max(Number(fourteen.style.height.slice(0, -2)), fourteen.scrollHeight)
            let fifteen = document.getElementById(id + '.15')
            let fif = Math.max(Number(fifteen.style.height.slice(0, -2)), fifteen.scrollHeight)
            let sixteen = document.getElementById(id + '.16')
            let six = Math.max(Number(sixteen.style.height.slice(0, -2)), sixteen.scrollHeight)
            let max = Math.max(f, s, t, fo, fi, si, se, ei, ni, te, el, tw, th, four, fif, six)
            let l = [f,s,t,fo,fi,si,se,ei,ni,te,el,tw,th,four,fif,six]
            let obj = [first,second,third,fourth,fifth,sixth,seventh,eighth,ninth,tenth,eleventh,twelfth,thirteen,fourteen,fifteen,sixteen]
            if(event){
                if(document.getElementById(event.target.id).scrollHeight == max){
                    for(let i = 1; i <= 16; i++) document.getElementById(id + '.' + i).style.height = document.getElementById(event.target.id).scrollHeight + "px"
                }
            }
            else{
                for(let i = 1; i <= 16; i++){
                    document.getElementById(id + '.' + i).style.height = 'auto'
                }
                for(let i = 1; i <= 16; i++){
                    document.getElementById(id + '.' + i).style.height = obj[l.lastIndexOf(max)].scrollHeight + 2 + "px"
                }
            }
        },
        update_step1(item, num){
            this.step1[item][num - 1] = document.getElementById(item + '.' + num).value
        },
        filter(column, in_data, item){
            if(item != undefined){
                let checked = document.getElementById(column + '.' + item).checked
                let lst = this[in_data][item][1]
                if(checked == true){
                    this[in_data][item][0] = 1
                    for(let i = 0; i < lst.length; i++){
                        this.$set(this.step1[lst[i]], 16, this.step1[lst[i]][16] + 1)
                    }
                    let resize = setInterval(() => {
                        if(document.getElementById(lst[lst.length - 1] + '.1')){
                            clearInterval(resize)
                            for(let j = 0; j < lst.length; j++){
                                this.change_width(lst[j])
                            }
                        }
                    }, 100)
                }
                else{
                    this[in_data][item][0] = 0
                    document.getElementById(column).checked = false
                    for(let i = 0; i < lst.length; i++){
                        this.$set(this.step1[lst[i]], 16, this.step1[lst[i]][16] - 1)
                    }
                }
            }
            else{
                let checked = document.getElementById(column).checked
                if(checked){
                    let lst2 = []
                    for(let key in this[in_data]){
                        let checked = document.getElementById(column + '.' + key).checked
                        this[in_data][key][0] = 1
                        if(!checked){
                            let lst = this[in_data][key][1]
                            for(let i = 0; i < lst.length; i++){
                                lst2.push(this.step1.lastIndexOf(this.step1[lst[i]]))
                                this.$set(this.step1[lst[i]], 16, this.step1[lst[i]][16] + 1)
                            }
                        }
                        document.getElementById(column + '.' + key).checked = true
                    }
                    let resize = setInterval(() => {
                        if(document.getElementById(lst2[lst2.length - 1] + '.1')){
                            clearInterval(resize)
                            for(let j = 0; j < lst2.length; j++){
                                this.change_width(lst2[j])
                            }
                        }
                    }, 100)
                }
                else{
                    for(let i = 0; i < this.step1.length; i++){
                        this.$set(this.step1[i], 16, this.step1[i][16] - 1)
                    }
                    for(let key in this[in_data]){
                        document.getElementById(column + '.' + key).checked = false
                        this[in_data][key][0] = 0
                    }
                }
            }
        },
        change_step(val){
            if(val == '-'){
                if(this.step != 0) this.step -= 1
            }
            else if(this.step != 1){
                this.step += 1
            }
            if(this.step == 1){
                let lst = []
                for(let i = 0; i < this.step1.length; i++){
                    if(this.step1[i][16] == 15) lst.push(i)
                }
                let resize = setInterval(() => {
                    if(document.getElementById(lst[lst.length - 1] + '.1')){
                        clearInterval(resize)
                        for(let j = 0; j < lst.length; j++){
                            this.change_width(lst[j])
                        }
                        let data = ['companies','spheres','speciality_names','skill_levels','need_2021','need_2022','need_2023','y_n_expert_count','corp_prep_program','mtb_for_prepare','make_mtb','org_city','responsible_person','post','phone','card_email']
                        let data1 = ['company','sphere','speciality_name','skill_level','need_2021','need_2022','need_2023','y_n_expert_count','corp_prep_program','mtb_for_prepare','make_mtb','org_city','responsible_person','post','phone','card_email']
                        for(let in_data in data){
                            for(let obj in this[data[in_data]]){
                                if(this[data[in_data]][obj][0] == 0) document.getElementById(data1[in_data] + '.' + obj).checked = false
                            }
                        }
                    }
                }, 100)
            }
        },
        update_step0(){
            this.card_name = document.getElementById('1.1').value
        },
    }
}
</script>

