<template>
    <div class="main">
        <div class="warp">
            <div v-if="role != 'admin'">
                <h3>{{no_admin_text}}</h3>
            </div>
            <div v-else class="users block">
                <h3>Добавить пользователя:</h3>
                <form class="row justify-content-center">
                    <div class="form-group col-6 col-md-4"> 
                        <input type="text" class="form-control newUserEmail formInput" name="newUserEmail" placeholder="Логин">
                    </div>
                    <div class="form-group col-6 col-md-4"> 
                        <input type="password" class="form-control newUserPassword formInput" name="newUserPassword" placeholder="Пароль">
                    </div>
                    <div class="form-group col-10 col-md-3">
                        <select id="role" class="form-control formInput" style="padding: 1px 7px">
                            <option value="choose_role" selected>Выберите роль</option>
                            <option value="metodist">Методист по сбору корпоративных стандартов</option>
                            <option value="company">Предприятие</option>
                        </select>
                    </div>
                    <div class="col-2 col-md-1"> 
                        <button class="btn btn-red-orange btn-almbb-small" @click="AddNewUser()" style="border-radius: 50%; font-size: 1.2em"><i class="fas fa-plus"></i></button>
                    </div>
                </form>
                <hr>
                <div class="user row" v-for="(user, index) in users" :key="index">
                    <div v-if="user.role == 'metodist'" class="email col-10 col-md-11">{{user.email}} - Методист по сбору корпоративных стандартов</div>
                    <div v-else-if="user.role == 'company'" class="email col-10 col-md-11">{{user.email}} - Предприятие</div>
                    <div class="col-2 col-md-1"><button class="btn btn-almbb-small btn-almbb-danger" @click="deleteUser(user.email, index)"><i class="fas fa-trash-alt"></i></button></div>
                </div>
            </div>
        </div>
        <div class="footer"><Footer></Footer></div> 
    </div>
</template>

<script>
import Swal from 'sweetalert2';
import Footer from './footer.vue';
export default {
    name: 'Admin',
    components: { Footer },
    data(){
        return{
            email: this.$store.getters.email,
            SessionID: this.$store.getters.SessionID,
            role: '',
            users: [],
            no_admin_text: '',
        }
    },
    beforeMount(){
        fetch(this.$store.state.serverIp+'/api/getInformation', {
            method: 'POST',
            body: JSON.stringify({email: this.email, sessionid: this.SessionID}),
            headers: {
                "Content-Type": "application/json"
            },
        })
        .then(response => {
            // console.log("res", response)
            return response.json()
        })
        .then(data => {
            if(data == '310'){
                document.cookie = "email=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                document.cookie = "SessionID=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                window.location.reload()
            }   
            this.role = data.role
            this.no_admin_text = "Вы не являетесь администратором!"
        })
        .catch(err => {
            console.log(err)
        })
        
        fetch(this.$store.state.serverIp+'/api/getUserList', {
            method: 'POST',
            body: JSON.stringify({email: this.email, sessionid: this.SessionID}),
            headers: {
                "Content-Type": "application/json"
            },
        })
        .then(response => {
            // console.log("res", response)
            return response.json()
        })
        .then(data => {
            if(data == '310'){
                document.cookie = "email=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                document.cookie = "SessionID=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                window.location.reload()
            }
            this.users = data
        })
        .catch(err => {
            console.log(err)
        })
    },
    methods: {
        AddNewUser(){
            event.preventDefault()
            let form = document.forms[0]
            let email = form.elements.newUserEmail.value
            let password = form.elements.newUserPassword.value
            let role = form.elements.role.value
            if(email == ''){
                Swal.fire({
                    icon: 'error',
                    title: 'Введите логин'
                })
            }
            else if(password.trim() == ''){
                Swal.fire({
                    icon: 'error',
                    title: 'Введите пароль'
                })
            }
            else if(role == 'choose_role'){
                Swal.fire({
                    icon: 'error',
                    title: 'Выберите роль'
                })
            }
            else{
                fetch(this.$store.state.serverIp+'/api/regUser', {
                    method: 'POST',
                    body: JSON.stringify({email: this.email, sessionid: this.SessionID, user: {email: email, password: password, role: role}}),
                    headers: {
                        "Content-Type": "application/json"
                    },
                })
                .then(response => {
                    // console.log("res", response)
                    return response.json()
                })
                .then(data => {
                    if(data.data == '310'){
                        document.cookie = "email=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                        document.cookie = "SessionID=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                        window.location.reload()
                    }
                    if(data.data == 'Reg successful'){
                        Swal.fire({
                            icon: 'success',
                            title: 'Пользователь успешно добавлен!',
                            timer: 1500,
                            timerProgressBar: true,
                        }).then(()=>{
                            this.users.push({email: email, role: role})
                        })
                    }
                    else if(data.data == 'Reg Fail'){
                        Swal.fire({
                            icon: 'error',
                            title: 'Такой пользователь уже существует',
                            timer: 1500,
                            timerProgressBar: true,
                        })
                    }
                })
                .catch(err => {
                    console.log(err)
                })
            }
        },
        deleteUser(delEmail, index){
            fetch(this.$store.state.serverIp+'/api/delUser', {
                method: 'POST',
                body: JSON.stringify({email: this.email, sessionid: this.SessionID, dopemail: delEmail}),
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                // console.log("res", response)
                return response.json()
            })
            .then(data => {
                if(data.data == '310'){
                    document.cookie = "email=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                    document.cookie = "SessionID=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                    window.location.reload()
                }
                if(data.data == 'User undefined'){
                    Swal.fire({
                        icon: 'error',
                        title: 'Пользователь не найден!',
                        timer: 1500,
                        timerProgressBar: true,
                    })
                }
                if (data.data == 'OK'){
                    Swal.fire({
                        icon: 'success',
                        title: 'Пользователь удален!',
                        timer: 1500,
                        timerProgressBar: true,
                    }).then(()=>{
                        // window.location.reload()
                        this.$delete(this.users, index)
                    })
                }
            })
            .catch(err => {
                console.log(err)
            })
        },
    },
}
</script>

<style scoped>
.warp{
    flex: 1 0 auto;
    /* padding: 0px 30px; */
}
.footer{
    flex: 0 0 auto;
}
.main{
    display: flex;
	flex-direction: column;
    min-height: 100vh;
}
.warp{
    min-height: 100%;
}
.block{
    background-color: #ffffff;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    -webkit-box-shadow: 0px 2px 5px 1px rgba(34, 60, 80, 0.18);
    -moz-box-shadow: 0px 2px 5px 1px rgba(34, 60, 80, 0.18);
    box-shadow: 0px 2px 5px 1px rgba(34, 60, 80, 0.18);
    min-height: 200px;
    padding: 15px 20px;
}
.formInput{
    border-radius: 50px;
}
.user:hover{
    text-decoration: none;
    color: black;
    background-color: rgba(236, 236, 236, 0.466);
}
.user{
    border: 1px solid rgba(0,0,0,.125);
    margin-bottom: 0.4em;
    border-radius: 0.25em;
    padding: 0.3em;
}
.email, .role{
    font-size: 1.3em;
    font-weight: bold;
}
.users {
    margin-bottom: 1em;
}
@media (max-width:767px) {
    .warp {
        padding-right: 5px;
        padding-left: 5px;
        padding-top: 90px !important;
    }
}
@media (min-width:768px) {
    .warp {
        padding-right: 50px;
        padding-left: 50px;
        padding-top: 150px !important;
    }
}
</style>