<template>
    <div class="main">
        <div class="container warp">
            <div class="pblock">
                <div class="email">{{email}}</div>
                <br>
                <form>
                    <div class="form-group row">
                        <div class="col-12"> 
                            <input type="password" class="form-control oldPassword formInput" name="oldPassword" placeholder="Старый пароль">
                        </div>
                    </div>
                    <div class="form-group row">
                        <div class="col-12"> 
                            <input type="password" class="form-control newPassword formInput" name="newPassword" placeholder="Новый пароль">
                        </div>
                    </div>
                    <div class="form-group row">
                        <div class="col-12"> 
                            <input type="password" name="returnNewPassword" class="form-control returnNewPassword formInput" placeholder="Повторите пароль">
                        </div>
                    </div>
                    <div class="form-group"> 
                        <button class="btn btn-rounded-blue btn-lg" @click="changePassword()">Сохранить</button>
                    </div>
                </form>
            </div>
        </div>
        <div class="footer"><Footer></Footer></div> 
    </div>
</template>

<script>
import Swal from 'sweetalert2'
import Footer from './footer.vue';
export default {
    components: { Footer },
    name: 'Profile',
    data(){
        return{
            email: this.$store.getters.email,
            SessionID: this.$store.getters.SessionID,
        }
    },
    beforeMount(){
        if (this.email == undefined) window.location.href = '/login'
    },
    methods: {
        changePassword(){
            event.preventDefault()
            let form = document.forms[0]
            let oldPassword = form.elements.oldPassword.value
            let password = form.elements.newPassword.value
            let returnPassword = form.elements.returnNewPassword.value
            if(password.trim() == ''){
                Swal.fire({
                    icon: 'error',
                    title: 'Введите пароль'
                })
            }
            else if(returnPassword.trim() == ''){
                Swal.fire({
                    icon: 'error',
                    title: 'Повторите пароль'
                })
            }
            else if(password != returnPassword){
                Swal.fire({
                    icon: 'error',
                    text: 'Пароли не совпадают'
                });
            }
            else{
                fetch(this.$store.state.serverIp+'/api/passUpdate', {
                    method: 'POST',
                    body: JSON.stringify({email: this.email, sessionid: this.SessionID, newpass: password, oldpass: oldPassword}),
                    headers: {
                        "Content-Type": "application/json"
                    },
                })
                .then(response => {
                    // console.log("res", response)
                    return response.json()
                })
                .then(data => {
                    if(data.data == '310'){
                        document.cookie = "email=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                        document.cookie = "SessionID=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                        window.location.reload()
                    }
                    if(data.data == 'Incorect password'){
                        Swal.fire({
                            icon: 'error',
                            title: 'Старый пароль введен не верно!',
                            timer: 1500,
                            timerProgressBar: true,
                        })
                    }
                    if (data.data == 'OK'){
                        Swal.fire({
                            icon: 'success',
                            title: 'Пароль успешно сменен!',
                            timer: 1500,
                            timerProgressBar: true,
                        }).then(()=>{
                            window.location.reload()
                        })
                    }
                })
                .catch(err => {
                    console.log(err)
                })
            }
        },
    },
}
</script>

<style scoped>
.warp{
    flex: 1 0 auto;
    padding-top: 100px !important;
    padding: 0px 50px;
}
.main{
    display: flex;
	flex-direction: column;
}
.main{
    height: 100%;
    padding: 0px;
    min-height: 100vh;
    margin-bottom: 0px;
}
.warp{
    padding-right: 50px;
    padding-left: 50px;
    min-height: 100%;
}
.pblock{
    background-color: #ffffff;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    -webkit-box-shadow: 0px 2px 5px 1px rgba(34, 60, 80, 0.18);
    -moz-box-shadow: 0px 2px 5px 1px rgba(34, 60, 80, 0.18);
    box-shadow: 0px 2px 5px 1px rgba(34, 60, 80, 0.18);
    min-height: 300px;
    padding: 15px 20px;
}
.pblock{
    margin-top: 15%;
}
.email{
    font-size: 1.3em;
    font-weight: bold;
}
.formInput{
    border-radius: 50px;
}
</style>