                                        <div class="col-2 col-md-1"><button class="btn btn-almbb-danger btn-almbb-small" @click="delete_card(item.tableid)"> <i class="fas fa-trash-alt"></i> </button></div>
                                        <div class="col-12" style="font-weight: lighter;">Автор: {{item.email}} - предприятие, обновлено {{item.update_user}} {{getTime(item.update_time)}}</div>
