<script>
import Swal from 'sweetalert2'
import Footer from './footer.vue';
export default {
    name: 'YourCards',
    components: { Footer },
    data(){
        return{
            email: this.$store.getters.email,
            SessionID: this.$store.getters.SessionID,
            role: this.$store.state.role,
            data: [],
            cards: [],
            what_show: 1,
        }
    },
    beforeMount(){
        if (this.email == undefined) window.location.href = '/login'
        else if(this.$store.getters.role == 'metodist' || this.$store.getters.role == 'company'){
            fetch(this.$store.state.serverIp+'/api/getDataList', {
                method: 'POST',
                body: JSON.stringify({email: this.email, sessionid: this.SessionID}),
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                // console.log("res", response)
                return response.json()
            })
            .then(data => {
                if(data.data == '310'){
                    document.cookie = "email=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                    document.cookie = "SessionID=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                    window.location.reload()
                }
                this.data = data.data
                for(let i = 0; i < data.data.length; i++){
                    this.cards.push({ job: data.data[i].data.job, company: data.data[i].data.company, sphere: data.data[i].data.sphere, tableid: data.data[i].tableid, role: data.data[i].role, email: data.data[i].email, update_time: data.data[i].data.update_time, update_user: data.data[i].data.update_user})
                }
            })
            .catch(err => {
                console.log(err)
            })
        }
        else if(this.$store.getters.role == 'admin'){
            fetch(this.$store.state.serverIp+'/api/getAllData', {
                method: 'POST',
                body: JSON.stringify({email: this.email, sessionid: this.SessionID}),
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                // console.log("res", response)
                return response.json()
            })
            .then(data => {
                this.data = data.data
                for(let i = 0; i < data.data.length; i++){
                    this.cards.push({ job: data.data[i].data.job, company: data.data[i].data.company, sphere: data.data[i].data.sphere, card_name: data.data[i].data.card_name, tableid: data.data[i].tableid, role: data.data[i].role, email: data.data[i].email, update_time: data.data[i].data.update_time, update_user: data.data[i].data.update_user})
                }
            })
            .catch(err => {
                console.log(err)
            })
        }
    },
    methods:{
        edit_card(id){
            if(this.email == 'admin' && this.what_show == 1) document.location.pathname = '/edit-filter-card/' + id
            else document.location.pathname = '/edit-card/' + id
        },
        delete_card(id){
            Swal.fire({
                text: 'Вы уверены, что хотите удалить эту карточку?',
                showCancelButton: true,
                cancelButtonText: 'Отмена',
                preConfirm: () => {
                    fetch(this.$store.state.serverIp+'/api/deleteData', {
                        method: 'POST',
                        body: JSON.stringify({email: this.email, sessionid: this.SessionID, tableid : id}),
                        headers: {
                            "Content-Type": "application/json"
                        },
                    })
                    .then(response => {
                        // console.log("res", response)
                        return response.json()
                    })
                    .then(data => {
                        if(data.data == '310'){
                            document.cookie = "email=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                            document.cookie = "SessionID=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                            window.location.reload()
                        }
                        else if(data.data == 'OK'){
                            Swal.fire({
                                icon: 'success',
                                text: 'Файл успешно удален',
                                showConfirmButton: false,
                                timer: 1500,
                            });
                            for(let i = 0; i < this.cards.length; i++){
                                if(this.cards[i].tableid == id){
                                    this.cards.splice(i, 1)
                                    return
                                }
                            }
                        }
                    })
                    .catch(err => {
                        console.log(err)
                    })     
                }
            });
        },
        export_to_excel(id){
            let num
            let role_for_admin
            for(let i = 0; i < this.data.length; i++){
                if(this.data[i].tableid == id){
                    num = i
                    role_for_admin = this.data[i].role
                }
            }
            const ExportJsonExcel = require("js-export-excel");
            if(this.email == 'admin' && this.what_show == 1){
                let option = {};
                option.fileName = this.data[num].data.card_name;
                let data2 = []
                
                data2.push({one: 'Наименование специальности / компетенции / заказчик', two: 'Уровень подготовки', three: 'Потребность в подготовке 2021', four: 'Потребность в подготовке 2022', five: 'Потребность в подготовке 2023', six: 'Да/нет/эксперт/количество', seven: 'Имеется ли корпоративная программа подготовки', eight: 'Наличие МТБ для подготовки', nine: 'Сколько стоит создать МТБ', ten: 'Город расположения организации', eleven: 'ФИО ответственного', twelve: 'Должность', thirteen: 'Телефон', fourteen: 'Почта'})
                for(let j = 0; j < this.data[num].data.step1.length; j++){
                    if(this.data[num].data.step1[j][16] == 15) data2.push({one: this.data[num].data.step1[j][0], two: this.data[num].data.step1[j][1], three: this.data[num].data.step1[j][2], four: this.data[num].data.step1[j][3], five: this.data[num].data.step1[j][4], six: this.data[num].data.step1[j][5], seven: this.data[num].data.step1[j][6], eight: this.data[num].data.step1[j][7], nine: this.data[num].data.step1[j][8], ten: this.data[num].data.step1[j][9], eleven: this.data[num].data.step1[j][10], twelve: this.data[num].data.step1[j][11], thirteen: this.data[num].data.step1[j][12], fourteen: this.data[num].data.step1[j][13]})
                }
                option.datas = [
                {
                    sheetData: data2,
                    sheetName: "Информация о потребностях",
                    sheetFilter: ["one", "two", "three", "four", "five", "six", "seven", "eight", "nine", "ten", "eleven", "twelve", "thirteen", "fourteen"],
                    columnWidths: [10, 10, 10, 10, 10, 10, 10, 10, 10, 10, 10, 10, 10, 10, 10],
                },
                ];
                let toExcel = new ExportJsonExcel(option); //new
                toExcel.saveExcel(); //保存
            }
            else if(this.$store.getters.role == 'metodist' || role_for_admin == 'metodist'){
                var option = {}; 
                option.fileName = this.data[num].data.job + ', ' + this.data[num].data.company;
                let data1 = []
                let data2 = []
                let data3 = []
                let n = 1
                for(let i = 0; i < this.data[num].data.step1.length; i++) data1.push({one: this.data[num].data.step1[i][0], two: this.data[num].data.step1[i][1], three: this.data[num].data.step1[i][2]})
                for(let i = 0; i < this.data[num].data.step2.length; i++){
                    if(i != 0){
                        data2.push({one: '', two: '', three: '', four: '', five: '', six: '', seven: '', eight: '', nine: '', ten: ''})
                        data2.push({one: '', two: '', three: '', four: '', five: '', six: '', seven: '', eight: '', nine: '', ten: ''})
                    }
                    data2.push({one: this.data[num].data.step1[i][0], two: '', three: '', four: '', five: '', six: '', seven: '', eight: '', nine: '', ten: ''})
                    data2.push({one: '№', two: 'ПЗ', three: 'КОС', four: 'Знания', five: 'Ссылка в сети Интернет на открытую информацию', six: 'Продвинутый уровень', seven: 'Начальный уровень', eight: 'Инструмент', nine: 'Оборудование', ten: 'Расходный материал'})
                    for(let j = 0; j < this.data[num].data.step2[i].length; j++){
                        data2.push({one: this.data[num].data.step2[i][j][0], two: this.data[num].data.step2[i][j][1], three: this.data[num].data.step2[i][j][2], four: this.data[num].data.step2[i][j][3], five: this.data[num].data.step2[i][j][4], six: this.data[num].data.step2[i][j][5], seven: this.data[num].data.step2[i][j][6], eight: this.data[num].data.step2[i][j][7], nine: this.data[num].data.step2[i][j][8], ten: this.data[num].data.step2[i][j][9]})
                        data3.push({one: n, two: this.data[num].data.step2[i][j][0], three: this.data[num].data.step2[i][j][10], four: this.data[num].data.step2[i][j][11], five: this.data[num].data.step2[i][j][12], six: this.data[num].data.step2[i][j][13]})
                        n++
                    }
                }
                option.datas = [
                {
                    sheetData: [
                        { one: this.data[num].data.job, two: this.data[num].data.company },
                        // { one: "1", two: "2" },
                    ],
                    sheetName: "Должность и предприятие",
                    sheetFilter: ["one", "two"],
                    sheetHeader: ["Должность", "Предприятие"],
                    columnWidths: [10, 10],
                },
                {
                    sheetData: data1,
                    sheetName: "Результат обучения",
                    sheetFilter: ["one", "two", "three"],
                    sheetHeader: ["Навык", "Трудовая операция", "Показатель оценки трудовой операции"],
                    columnWidths: [10, 20, 30],
                },
                {
                    sheetData: data2,
                    sheetName: "Содержание образ. программы",
                    sheetFilter: ["one", "two", "three", "four", "five", "six", "seven", "eight", "nine", "ten"],
                    columnWidths: [3, 3, 12, 12, 7, 6, 6, 5, 5, 5],
                },
                {
                    sheetData: data3,
                    sheetName: "Конструктор программы",
                    sheetFilter: ["one", "two", "three", "four", "five", "six"],
                    sheetHeader: ["№", "Наименование темы", "16 часов", "72 часа", "144 часа", "256 часов"],
                    columnWidths: [2, 5, 5, 5, 5, 5],
                },
                ];
                
                let toExcel = new ExportJsonExcel(option); //new
                toExcel.saveExcel(); //保存
            }
            else if(this.$store.getters.role == 'company' || role_for_admin == 'company'){
                let option = {};
                option.fileName = this.data[num].data.company + ', ' + this.data[num].data.sphere;
                let data2 = []
                
                data2.push({one: 'Наименование специальности / компетенции / заказчик', two: 'Уровень подготовки', three: 'Потребность в подготовке 2021', four: 'Потребность в подготовке 2022', five: 'Потребность в подготовке 2023', six: 'Да/нет/эксперт/количество', seven: 'Имеется ли корпоративная программа подготовки', eight: 'Наличие МТБ для подготовки', nine: 'Сколько стоит создать МТБ'})
                for(let j = 0; j < this.data[num].data.step1.length; j++){
                    data2.push({one: this.data[num].data.step1[j][0], two: this.data[num].data.step1[j][1], three: this.data[num].data.step1[j][2], four: this.data[num].data.step1[j][3], five: this.data[num].data.step1[j][4], six: this.data[num].data.step1[j][5], seven: this.data[num].data.step1[j][6], eight: this.data[num].data.step1[j][7], nine: this.data[num].data.step1[j][8]})
                }
                option.datas = [
                {
                    sheetData: [
                        { one: this.data[num].data.company, two: this.data[num].data.sphere, three: this.data[num].data.org_city, four: this.data[num].data.responsible_person, five: this.data[num].data.post, six: this.data[num].data.phone, seven: this.data[num].data.card_email},
                        // { one: "1", two: "2" },
                    ],
                    sheetName: "Информация о предприятии",
                    sheetFilter: ["one", 'two', 'three', 'four', 'five', 'six', 'seven'],
                    sheetHeader: ["Название предприятия", "Сфера", 'Город расположения организации', 'ФИО ответственного', 'Должность', 'Телефон', 'Почта'],
                    columnWidths: [10, 10, 10, 10, 10, 10, 10],
                },
                {
                    sheetData: data2,
                    sheetName: "Информация о потребностях",
                    sheetFilter: ["one", "two", "three", "four", "five", "six", "seven", "eight", "nine", "ten", "eleven", "twelve", "thirteen", "fourteen"],
                    columnWidths: [10, 10, 10, 10, 10, 10, 10, 10, 10, 10, 10, 10, 10, 10, 10],
                },
                ];
                let toExcel = new ExportJsonExcel(option); //new
                toExcel.saveExcel(); //保存
            }
        },
        getTime(time){
            let now = new Date()
            let difference = now.getTimezoneOffset()
            let send_time = new Date(time[0], time[1], time[2], time[3] - Math.floor(difference / 60), time[4] - difference % 60)
            let houres
            let minites
            let day
            let month = send_time.getMonth() + 1
            let year = send_time.getFullYear()
            if(send_time.getHours() < 10) houres = '0' + send_time.getHours()
            else houres = send_time.getHours()
            if(send_time.getMinutes() < 10) minites = '0' + send_time.getMinutes()
            else minites = send_time.getMinutes()
            if(send_time.getDate() < 10) day = '0' + send_time.getDate()
            else day = send_time.getDate()
            if(send_time.getMonth() < 9) month = '0' + (send_time.getMonth() + 1)
            else month = send_time.getMonth() + 1
            return day + '.' + month + '.' + year + ' в ' + houres + ':' + minites
        },
        filter(num, lst){
            for(let i in lst) document.getElementById(lst[i]).checked = false
            this.what_show = num
        },
    },
}
</script>

