 <template>
    <div class="main">
        <div v-if="this.$store.getters.role == 'metodist' || role_for_admin == 'metodist'">
            <div class="place" v-if="step != 0 && job != '' && company != ''"><div class="name text-center">{{job + ', ' + company}}</div></div>
            <div class="place" v-else-if="step != 0"><div class="name text-center">Данные о компании или должности не заполнены</div></div>
            <table id="table" style="width: 100%;" v-if="step == 0">
                <tr class="trstyle">
                    <th class="thead elmincentertop">Должность</th>
                    <th class="thead elmincentertop">Предприятие</th>
                </tr>
                <tr class="trstyle2">
                    <td class="elmincenter" style="padding: 0px"><input type="text" class="tinput" id="1.1" :value="job" @input="update_step0(1)"></td>
                    <td class="elmincenter" style="padding: 0px;"><input type="text" class="tinput" id="1.2" :value="company" @input="update_step0(2)"></td>
                    <!-- <td style="padding: 0px"><input type="text" class="tinput"></td> -->
                </tr>
            </table>
            <table id="table2" style="width: 100%;" v-if="step == 1">
                <tr class="trstyle">
                    <th class="thead">Навык</th>
                    <th class="thead elmincentertop">Трудовая операция</th>
                    <th class="thead elmincentertop">Показатель оценки трудовой операции</th>
                    <th class="thead"></th>
                </tr>
                <tr v-for="item in rows1" :key="item" class="trstyle2" :id="item">
                    <td style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.1'" :value="step1[item][0]" @input="update_step1(item, 1), change_width()"></textarea></td>
                    <td class="elmincenter" style="padding: 0px;"><textarea type="text" class="tinput" :id="item +'.2'" :value="step1[item][1]" @input="update_step1(item, 2), change_width()"></textarea></td>
                    <td class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.3'" :value="step1[item][2]" @input="update_step1(item, 3), change_width()"></textarea></td>
                    <td><button class="delb" @click="del_row(item)"><i class="fas fa-times"></i></button></td>
                </tr>
            </table>
            <table id="table3" style="width: 100%;" v-if="step == 2">
                <caption style="background-color: yellow; width: 100%; caption-side: top; color: black; font-size: 1.1em; text-align: center; font-weight: bold;">{{step1[dop_step][0]}}</caption>
                <tr class="trstyle">
                    <th class="thead" rowspan="2">№</th>
                    <th class="thead elmincentertop" rowspan="2">ПЗ</th>
                    <th class="thead elmincentertop" rowspan="2">КОС</th>
                    <th class="thead elmincentertop" rowspan="2">Знания</th>
                    <th class="thead elmincentertop" rowspan="2">Ссылка в сети на открытую информацию</th>
                    <th class="thead elmincentertop" rowspan="2">Продвинутый уровень</th>
                    <th class="thead elmincentertop" rowspan="2">Начальный уровень</th>
                    <th class="thead elmincentertop" rowspan="1" colspan="3" style="border-bottom: 2px solid #c8cccf;">МТБ</th>
                    <th class="thead" rowspan="2"></th>
                </tr>
                <tr class="trstyle">
                    <th class="thead elmincentertop">Инструмент</th>
                    <th class="thead elmincentertop">Оборудование</th>
                    <th class="thead elmincentertop">Расходный материал</th>
                </tr>
                <tr v-for="(item, index) in rows2[dop_step]" :key="item" class="trstyle2" :id="index">
                    <td style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.1'" :value="step2[dop_step][item][0]" @input="update_step2(item, 1), change_width()" style="width: 250px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px;"><textarea type="text" class="tinput" :id="item +'.2'" :value="step2[dop_step][item][1]" @input="update_step2(item, 2), change_width()" style="width: 250px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px;"><textarea type="text" class="tinput" :id="item +'.3'" :value="step2[dop_step][item][2]" @input="update_step2(item, 3), change_width()" style="width: 250px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px;"><textarea type="text" class="tinput" :id="item +'.4'" :value="step2[dop_step][item][3]" @input="update_step2(item, 4), change_width()" style="width: 250px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px;"><textarea type="text" class="tinput" :id="item +'.5'" :value="step2[dop_step][item][4]" @input="update_step2(item, 5), change_width()" style="width: 250px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px;" :id="item +'.6'">
                        Выполнение совместно с экспертом (ч): <input type="text" class="tinput" :id="item +'.6.1'" :value="step2[dop_step][item][5][0]" @input="update_step2(item, 6, 1)" style="width: 250px;">
                        Выполнение сомостоятельное + онализ ошибок (ч): <input type="text" class="tinput" :id="item +'.6.2'" :value="step2[dop_step][item][5][1]" @input="update_step2(item, 6, 2)" style="width: 250px;">
                        Выполнение самостоятельное (повторное) (ч): <input type="text" class="tinput" :id="item +'.6.3'" :value="step2[dop_step][item][5][2]" @input="update_step2(item, 6, 3)" style="width: 250px;">
                        Выполнение контрольное (ч): <input type="text" class="tinput" :id="item +'.6.4'" :value="step2[dop_step][item][5][3]" @input="update_step2(item, 6, 4)" style="width: 250px;">
                        Итого (ч): <div :id="item + '.6.5'" style="width: 250px;">{{count_hours(item, 6)}}</div>
                    </td>
                    <td class="elmincenter" style="padding: 0px;" :id="item +'.7'">
                        Выполнение совместно с экспертом (ч): <input type="text" class="tinput" :id="item +'.7.1'" :value="step2[dop_step][item][6][0]" @input="update_step2(item, 7, 1)" style="width: 250px;">
                        Выполнение сомостоятельное + онализ ошибок (ч): <input type="text" class="tinput" :id="item +'.7.2'" :value="step2[dop_step][item][6][1]" @input="update_step2(item, 7, 2)" style="width: 250px;">
                        Выполнение самостоятельное (повторное) (ч): <input type="text" class="tinput" :id="item +'.7.3'" :value="step2[dop_step][item][6][2]" @input="update_step2(item, 7, 3)" style="width: 250px;">
                        Выполнение контрольное (ч): <input type="text" class="tinput" :id="item +'.7.4'" :value="step2[dop_step][item][6][3]" @input="update_step2(item, 7, 4)" style="width: 250px;">
                        Итого (ч): <div :id="item + '.7.5'" style="width: 250px;">{{count_hours(item, 7)}}</div>
                    </td>
                    <td class="elmincenter" style="padding: 0px;"><textarea type="text" class="tinput" :id="item +'.8'" :value="step2[dop_step][item][7]" @input="update_step2(item, 8), change_width()" style="width: 250px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px;"><textarea type="text" class="tinput" :id="item +'.9'" :value="step2[dop_step][item][8]" @input="update_step2(item, 9), change_width()" style="width: 250px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.10'" :value="step2[dop_step][item][9]" @input="update_step2(item, 10), change_width()" style="width: 250px;"></textarea></td>
                    <td><button class="delb" @click="del_row(index)"><i class="fas fa-times"></i></button></td>
                </tr>
            </table>
            <table id="table4" style="width: 100%;" v-if="step == 3">
                <tr class="trstyle">
                    <th class="thead" rowspan="2" style="border-bottom: 2px solid #c8cccf;">№</th>
                    <th class="thead elmincentertop" rowspan="2" style="border-bottom: 2px solid #c8cccf;">Наименование темы</th>
                    <th class="thead" colspan="4" style="border-bottom: 2px solid #c8cccf;">Количество часов</th>
                </tr>
                <tr class="trstyle">
                    <th class="thead elmincentertop">16</th>
                    <th class="thead elmincentertop">72</th>
                    <th class="thead elmincentertop">144</th>
                    <th class="thead">256</th>
                </tr>
                <tr v-for="(item, index) in step3" :key="item" class="trstyle2">
                    <th class="thead" style="padding: 0px">{{index + 1}}</th>
                    <th class="elmincentertop thead" style="padding: 0px">{{make_normal(item[0])}}</th>
                    <td class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="Number(index + 1) +'.1'" :value="item[1]" @input="update_step3(index + 1, 1), change_width()"></textarea></td>
                    <td class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="Number(index + 1) +'.2'" :value="item[2]" @input="update_step3(index + 1, 2), change_width()"></textarea></td>
                    <td class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="Number(index + 1) +'.3'" :value="item[3]" @input="update_step3(index + 1, 3), change_width()"></textarea></td>
                    <td style="padding: 0px"><textarea type="text" class="tinput" :id="Number(index + 1) +'.4'" :value="item[4]" @input="update_step3(index + 1, 4), change_width()"></textarea></td>
                </tr>
            </table>
            <br>
            <div class="row justify-content-end">
                <div class="col-1">
                    <button class="btn btn-almbb-danger btn-almbb-small" v-if="step != 0 && step != 3" @click="rows_number('-')" style="border-radius: 50%; font-size: 1.2em"><i class="fas fa-minus"></i></button>
                </div>
                <div class="col-1">
                    <button class="btn btn-almbb-success btn-almbb-small" v-if="step != 0 && step != 3" @click="rows_number('+')" style="border-radius: 50%; font-size: 1.2em"><i class="fas fa-plus"></i></button>
                </div>
            </div>
            <br>
            <br>
            <br>
            <div class="row buttons">
                <div class="col-4">
                    <button class="btn btn-elegant swb" v-if="step != 0" @click="change_step('-')" style="width: 100%;">Назад</button>
                </div>
                <div class="col-4">
                    <button class="btn btn-almbb-success swb" @click="save()" style="width: 100%;">Сохранить</button>
                </div>
                <div class="col-4">
                    <button class="btn btn-blue swb" v-if="step != 3" style="width: 100%;" @click="change_step('+')">Далее</button>
                </div>
            </div>
            <button v-if="step != 0 && step != 3" class="btn questionBtn" @click="usefullInfo()"><i class="fas fa-question"></i></button>
        </div>
        <div v-if="this.$store.getters.role == 'company' || role_for_admin == 'company'">
            <div class="place" v-if="step != 0 && sphere != '' && company != ''"><div class="name text-center">{{company}}, {{sphere}}</div></div>
            <div class="place" v-else-if="step != 0"><div class="name text-center">Данные о карточке не заполнены</div></div>
            <table id="table" class="tb" v-if="step == 0">
                <tr class="trstyle">
                    <th class="thead elmincentertop">Название предприятия</th>
                    <th class="thead elmincentertop">Сфера <button v-if="other" @click="show_other(false)" type="button" class="btn btn-secondary">Вернуться к списку</button></th>
                    <th class="thead elmincentertop">Город расположения организации</th>
                    <th class="thead elmincentertop">ФИО ответственного</th>
                    <th class="thead elmincentertop">Должность</th>
                    <th class="thead elmincentertop">Телефон</th>
                    <th class="thead elmincentertop">Почта</th>
                </tr>
                <tr class="trstyle2">
                    <td class="elmincenter" style="padding: 0px"><input type="text" class="tinput" id="1.1" :value="company" @input="update_step0(1)" style="width: 250px;"></td>
                    <td class="elmincenter" style="padding: 0px">
                        <select v-if="!other" class="tinput" id="1.2" @input="update_step0(2)" style="width: 250px;">
                            <option selected>{{sphere}}</option>
                            <option>Транспорт и логистика</option>
                            <option>Агрокомплекс</option>
                            <option>Рыбная промышленность</option>
                            <option>Туризм</option>
                            <option>Сфера услуг</option>
                            <option>Строительство</option>
                            <option>Энергетика /нефть, газ, уголь, другие источники энергии/</option>
                            <option>ЖКХ</option>
                            <option>Другое</option>
                        </select>
                        <input v-if="other" type="text" class="tinput" id="1.2" :value="sphere" @input="update_step0(2)">
                    </td>
                    <td class="elmincenter" style="padding: 0px"><input type="text" class="tinput" id="1.3" :value="org_city" @input="update_step0(3)" style="width: 250px;"></td>
                    <td class="elmincenter" style="padding: 0px"><input type="text" class="tinput" id="1.4" :value="responsible_person" @input="update_step0(4)" style="width: 250px;"></td>
                    <td class="elmincenter" style="padding: 0px"><input type="text" class="tinput" id="1.5" :value="post" @input="update_step0(5)" style="width: 250px;"></td>
                    <td class="elmincenter" style="padding: 0px"><input type="text" class="tinput" id="1.6" :value="phone" @input="update_step0(6)" style="width: 250px;"></td>
                    <td class="elmincenter" style="padding: 0px"><input type="text" class="tinput" id="1.7" :value="card_email" @input="update_step0(7)" style="width: 250px;"></td>
                </tr>
            </table>
            <table id="table2" style="width: 100%;" v-if="step == 1">
                <tr class="trstyle">
                    <th class="thead" rowspan="2">Наименование специальности / компетенции / заказчик</th>
                    <th class="thead elmincentertop" rowspan="2">Уровень подготовки</th>
                    <th class="thead elmincentertop" rowspan="1" colspan="3" style="border-bottom: 2px solid #c8cccf;">Потребность в подготовке</th>
                    <th class="thead elmincentertop" rowspan="2">Да/нет/эксперт/количество</th>
                    <th class="thead elmincentertop" rowspan="2">Имеется ли корпоративная программа подготовки</th>
                    <th class="thead elmincentertop" rowspan="1" colspan="2" style="border-bottom: 2px solid #c8cccf;">Материально техническая база (МТБ)</th>
                    <th class="thead elmincentertop" rowspan="2"></th>
                </tr>
                <tr class="trstyle">
                    <th class="thead elmincentertop">2021</th>
                    <th class="thead elmincentertop">2022</th>
                    <th class="thead elmincentertop">2023</th>
                    <th class="thead elmincentertop">Наличие МТБ для подготовки</th>
                    <th class="thead elmincentertop">Сколько стоит создать МТБ</th>
                </tr>
                <tr v-for="item in rows1" :key="item" class="trstyle2" :id="item">
                    <td style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.1'" :value="step1[item][0]" @input="update_step1(item, 1), change_width()" style="width: 250px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px;"><textarea type="text" class="tinput" :id="item +'.2'" :value="step1[item][1]" @input="update_step1(item, 2), change_width()" style="width: 250px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.3'" :value="step1[item][2]" @input="update_step1(item, 3), change_width()" style="width: 100px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.4'" :value="step1[item][3]" @input="update_step1(item, 4), change_width()" style="width: 100px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.5'" :value="step1[item][4]" @input="update_step1(item, 5), change_width()" style="width: 100px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.6'" :value="step1[item][5]" @input="update_step1(item, 6), change_width()" style="width: 150px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.7'" :value="step1[item][6]" @input="update_step1(item, 7), change_width()" style="width: 150px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.8'" :value="step1[item][7]" @input="update_step1(item, 8), change_width()" style="width: 250px;"></textarea></td>
                    <td class="elmincenter" style="padding: 0px"><textarea type="text" class="tinput" :id="item +'.9'" :value="step1[item][8]" @input="update_step1(item, 9), change_width()" style="width: 250px;"></textarea></td>
                    <td><button class="delb" @click="del_row(item)"><i class="fas fa-times"></i></button></td>
                </tr>
            </table>
            <br>
            <div class="row justify-content-end">
                <div class="col-3 col-md-1">
                    <button class="btn btn-almbb-danger btn-almbb-small" v-if="step != 0" @click="rows_number('-')" style="border-radius: 50%; font-size: 1.2em"><i class="fas fa-minus"></i></button>
                </div>
                <div class="col-3 col-md-1">
                    <button class="btn btn-almbb-success btn-almbb-small" v-if="step != 0" @click="rows_number('+')" style="border-radius: 50%; font-size: 1.2em"><i class="fas fa-plus"></i></button>
                </div>
            </div>
            <br>
            <br>
            <br>
            <div class="row buttons">
                <div class="col-4">
                    <button class="btn btn-elegant swb" v-if="step != 0" @click="change_step('-')" style="width: 100%;">Назад</button>
                </div>
                <div class="col-4">
                    <button class="btn btn-almbb-success swb" @click="save()" style="width: 100%;">Сохранить</button>
                </div>
                <div class="col-4">
                    <button class="btn btn-blue swb" v-if="step != 1" style="width: 100%;" @click="change_step('+')">Далее</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// import 'table2excel';
// const Table2Excel = window.Table2Excel;
 
// const table2excel = new Table2Excel();
// import ExcellentExport from 'excellentexport';
import Swal from 'sweetalert2'
export default {
    name: 'EditCard',
    data(){
        return{
            rows1: [],
            rows2: [],
            step: 0,
            dop_step: -1,
            role_for_admin: '',
            job: '',
            company: '',
            sphere: '',
            org_city: '',
            responsible_person: '',
            post: '',
            phone: '',
            card_email: '',
            other: false,
            id: this.$route.params.id,
            step1: [],
            step2: [],
            step3: [],
            email: this.$store.getters.email,
            SessionID: this.$store.getters.SessionID,
            role: this.$store.getters.role,
            // ExcellentExport: ExcellentExport,
        }
    },
    beforeMount(){
        if (this.email == undefined) window.location.href = '/login'
        fetch(this.$store.state.serverIp + '/api/getData', {
            method: 'POST',
            body: JSON.stringify({email: this.email, sessionid: this.SessionID, tableid: this.id}),
            headers: {
                "Content-Type": "application/json"
            },
        })
        .then(response => {
            // console.log("res", response)
            return response.json()
        })
        .then((data) => {
            if(data.data == '310'){
                document.cookie = "email=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                document.cookie = "SessionID=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                window.location.reload()
            }
            this.job = data.data.data.job
            this.company = data.data.data.company
            this.sphere = data.data.data.sphere
            this.org_city = data.data.data.org_city
            this.responsible_person = data.data.data.responsible_person
            this.post = data.data.data.post
            this.phone = data.data.data.phone
            this.card_email = data.data.data.card_email
            this.role_for_admin = data.data.role
            this.rows1 = data.data.data.rows1
            this.rows2 = data.data.data.rows2
            this.step1 = data.data.data.step1
            this.step2 = data.data.data.step2
        })
        .catch(err => {
            Swal.fire({
                icon: 'error',
                text: 'Непредвиденная ошибка, повторите попытку позже'
            });
            console.log(err)
        })
    },
    beforeDestroy(){
        if(confirm("Сохранить изменения?")){
            let now = new Date()
            fetch(this.$store.state.serverIp+'/api/updateData', {
                method: 'POST',
                 body: JSON.stringify({email: this.email, sessionid: this.SessionID, role: this.role, tableid: this.id, data: {rows1: this.rows1, rows2: this.rows2, job: this.job, company: this.company, sphere: this.sphere, org_city: this.org_city, responsible_person: this.responsible_person, post: this.post, phone: this.phone, card_email: this.card_email, step1: this.step1, step2: this.step2, update_user: this.email, update_time: [now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate(), now.getUTCHours(), now.getUTCMinutes()]}}),
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                // console.log("res", response)
                return response.json()
            })
            .then(data => {
                if(data.data == '310'){
                    document.cookie = "email=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                    document.cookie = "SessionID=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                    window.location.reload()
                }
            })
            .catch(err => {
                console.log(err)
            })
        }
    },
    methods: {
        rows_number(val){
            if(this.step == 1){
                if(val == '+'){
                    if(this.$store.getters.role == 'metodist' || this.role_for_admin == 'metodist'){
                        this.step1.push(['', '', ''])
                        this.rows1.push(this.rows1.length)
                    }
                    else if(this.$store.getters.role == 'company' || this.role_for_admin == 'company'){
                        this.step1.push(['', '', '', '', '', '', '', '', ''])
                        this.rows1.push(this.rows1.length)
                    }
                }
                else if(val == '-'){
                    this.rows1.splice(-1, 1)
                    this.step1.splice(-1, 1)
                }
            }
            else if(this.step == 2){
                if(val == '+'){
                    this.step2[this.dop_step].push(['', '', '', '', '', [0, 0, 0, 0], [0, 0, 0, 0], '', '', '', '', '', '', ''])
                    this.rows2[this.dop_step].push(this.rows2[this.dop_step].length)
                }
                else if(val == '-'){
                    this.rows2[this.dop_step].splice(-1, 1)
                    this.step2[this.dop_step].splice(-1, 1)
                }
            }
        },
        del_row(id){
            Swal.fire({
                text: 'Вы уверены, что хотите удалить эту строку?',
                showCancelButton: true,
                cancelButtonText: 'Отмена',
                preConfirm: () => {
                    if(this.step == 1){
                        this.rows1.splice(-1, 1)
                        this.step1.splice(id, 1)
                    }
                    else if(this.step == 2){
                        this.rows2[this.dop_step].splice(-1, 1)
                        this.step2[this.dop_step].splice(id, 1)
                    }
                }
            })
        },
        save(){
            let now = new Date()
            fetch(this.$store.state.serverIp+'/api/updateData', {
                method: 'POST',
                body: JSON.stringify({email: this.email, sessionid: this.SessionID, role: this.role, tableid: this.id, data: {rows1: this.rows1, rows2: this.rows2, job: this.job, company: this.company, sphere: this.sphere, org_city: this.org_city, responsible_person: this.responsible_person, post: this.post, phone: this.phone, card_email: this.card_email, step1: this.step1, step2: this.step2, update_user: this.email, update_time: [now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate(), now.getUTCHours(), now.getUTCMinutes()]}}),
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                // console.log("res", response)
                return response.json()
            })
            .then(data => {
                if(data.data == '310'){
                    document.cookie = "email=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                    document.cookie = "SessionID=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                    window.location.reload()
                }   
                else{
                    Swal.fire({
                        icon: 'success',
                        text: 'Файл успешно обновлен',
                        showConfirmButton: false,
                        timer: 1500,
                    });
                }
            })
            .catch(err => {
                console.log(err)
            })
        },
        change_step(val){
            if(val == '-'){
                if(this.step != 2 || this.dop_step == 0) this.step -= 1
            }
            else{
                if(this.step != 2 || this.dop_step == this.step1.length - 1){
                    if(this.step == 1){
                        if(this.step1.length != 0) this.step += 1
                    }
                    else this.step += 1
                }
            }
            if(this.step == 1){
                if(this.$store.getters.role == 'metodist' || this.role_for_admin == 'metodist'){
                    this.dop_step = -1
                    let upd = setInterval(() => {
                        if(document.getElementById(this.step1.length - 1 + '.3') || this.step1.length == 0){
                            clearInterval(upd)
                            this.job = document.getElementById('1.1').value
                            this.company = document.getElementById('1.2').value
                            for(let i = 0; i < this.step1.length; i++){
                                this.change_width(i)
                            }
                        }
                    }, 100);
                }
                else if(this.$store.getters.role == 'company' || this.role_for_admin == 'company'){
                    this.company = document.getElementById('1.1').value
                    this.sphere = document.getElementById('1.2').value
                    let upd = setInterval(() => {
                        if(document.getElementById(this.step1.length - 1 + '.9') || this.step1.length == 0){
                            clearInterval(upd)
                            for(let i = 0; i < this.step1.length; i++){
                                this.change_width(i)
                            }
                        }
                    }, 100);
                }
            }
            else if(this.step == 2){
                if(this.dop_step == -1){
                    let len = this.step1.length - this.rows2.length
                    for(let i = 0; i < len; i++){
                        this.step2.push([])
                        this.rows2.push([])
                    }
                }
                if(val == "+") this.dop_step += 1
                else if(val == "-") this.dop_step -= 1
                let upd = setInterval(() => {
                    if(document.getElementById(this.step2[this.dop_step].length - 1 + '.10') || this.step2.length == 0){
                        clearInterval(upd)
                        for(let i = 0; i < this.step2[this.dop_step].length; i++){
                            this.change_width(i)
                        }
                    }
                }, 100);
            }
            else if(this.step == 3){
                this.step3 = []
                this.dop_step = this.step1.length
                for(let i = 0; i < this.step2.length; i++){
                    for(let j = 0; j < this.step2[i].length; j++){
                        this.step3.push([this.step2[i][j][0], this.step2[i][j][10], this.step2[i][j][11], this.step2[i][j][12], this.step2[i][j][13]])
                    }
                }
                let upd = setInterval(() => {
                    if(document.getElementById(this.step3.length + '.4') || this.step3.length == 0){
                        clearInterval(upd)
                        for(let i = 1; i <= this.step3.length; i++){
                            this.change_width(i)
                        }
                    }
                }, 100);
            }
        },
        change_width(item){
            let id
            if(item == undefined) id = event.target.id[0]
            else id = item
            if(this.step == 1){
                let first = document.getElementById(id + '.1')
                let f = Math.max(Number(first.style.height.slice(0, -2)), first.scrollHeight)
                let second = document.getElementById(id + '.2')
                let s = Math.max(Number(second.style.height.slice(0, -2)), second.scrollHeight)
                let third = document.getElementById(id + '.3')
                let t = Math.max(Number(third.style.height.slice(0, -2)), third.scrollHeight)
                if(this.$store.getters.role == 'metodist' || this.role_for_admin == 'metodist'){
                    let max = Math.max(f, s, t)
                    if(event){
                        if(document.getElementById(event.target.id).scrollHeight > max){
                            for(let i = 1; i < 4; i++) document.getElementById(id + '.' + i).style.height = document.getElementById(event.target.id).scrollHeight + "px"
                        }
                    }
                    else{
                        document.getElementById(id + '.1').style.height = 'auto'
                        document.getElementById(id + '.2').style.height = 'auto'
                        document.getElementById(id + '.3').style.height = 'auto'
                        if(max == f){
                            document.getElementById(id + '.1').style.height = first.scrollHeight + 2 + "px"
                            document.getElementById(id + '.2').style.height = first.scrollHeight + 2 + "px"
                            document.getElementById(id + '.3').style.height = first.scrollHeight + 2 + "px"
                        }
                        else if(max == s){
                            document.getElementById(id + '.1').style.height = second.scrollHeight + 2 + "px"
                            document.getElementById(id + '.2').style.height = second.scrollHeight + 2 + "px"
                            document.getElementById(id + '.3').style.height = second.scrollHeight + 2 + "px"
                        }
                        else{
                            document.getElementById(id + '.1').style.height = third.scrollHeight + 2 + "px"
                            document.getElementById(id + '.2').style.height = third.scrollHeight + 2 + "px"
                            document.getElementById(id + '.3').style.height = third.scrollHeight + 2 + "px"
                        }
                    }
                }
                else if(this.$store.getters.role == 'company' || this.role_for_admin == 'company'){
                    let fourth = document.getElementById(id + '.4')
                    let fo = Math.max(Number(fourth.style.height.slice(0, -2)), fourth.scrollHeight)
                    let fifth = document.getElementById(id + '.5')
                    let fi = Math.max(Number(fifth.style.height.slice(0, -2)), fifth.scrollHeight)
                    let sixth = document.getElementById(id + '.6')
                    let si = Math.max(Number(sixth.style.height.slice(0, -2)), sixth.scrollHeight)
                    let seventh = document.getElementById(id + '.7')
                    let se = Math.max(Number(seventh.style.height.slice(0, -2)), seventh.scrollHeight)
                    let eighth = document.getElementById(id + '.8')
                    let ei = Math.max(Number(eighth.style.height.slice(0, -2)), eighth.scrollHeight)
                    let ninth = document.getElementById(id + '.9')
                    let ni = Math.max(Number(ninth.style.height.slice(0, -2)), ninth.scrollHeight)
                    let max = Math.max(f, s, t, fo, fi, si, se, ei, ni)
                    let l = [f,s,t,fo,fi,si,se,ei,ni]
                    let obj = [first,second,third,fourth,fifth,sixth,seventh,eighth,ninth]
                    if(event){
                        if(document.getElementById(event.target.id).scrollHeight == max){
                            for(let i = 1; i <= 9; i++) document.getElementById(id + '.' + i).style.height = document.getElementById(event.target.id).scrollHeight + "px"
                        }
                    }
                    else{
                        for(let i = 1; i <= 9; i++){
                            document.getElementById(id + '.' + i).style.height = 'auto'
                        }
                        for(let i = 1; i <= 9; i++){
                            document.getElementById(id + '.' + i).style.height = obj[l.lastIndexOf(max)].scrollHeight + 2 + "px"
                        }
                    }
                }
            }
            else if(this.step == 2){
                let first = document.getElementById(id + '.1')
                let f = Math.max(Number(first.style.height.slice(0, -2)), first.scrollHeight)
                let second = document.getElementById(id + '.2')
                let s = Math.max(Number(second.style.height.slice(0, -2)), second.scrollHeight)
                let third = document.getElementById(id + '.3')
                let t = Math.max(Number(third.style.height.slice(0, -2)), third.scrollHeight)
                let fourth = document.getElementById(id + '.4')
                let fo = Math.max(Number(fourth.style.height.slice(0, -2)), fourth.scrollHeight)
                let fifth = document.getElementById(id + '.5')
                let fi = Math.max(Number(fifth.style.height.slice(0, -2)), fifth.scrollHeight)
                let sixth = document.getElementById(id + '.6')
                let si = Math.max(Number(sixth.style.height.slice(0, -2)), sixth.scrollHeight)
                let seventh = document.getElementById(id + '.7')
                let se = Math.max(Number(seventh.style.height.slice(0, -2)), seventh.scrollHeight)
                let eighth = document.getElementById(id + '.8')
                let ei = Math.max(Number(eighth.style.height.slice(0, -2)), eighth.scrollHeight)
                let ninth = document.getElementById(id + '.9')
                let ni = Math.max(Number(ninth.style.height.slice(0, -2)), ninth.scrollHeight)
                let tenth = document.getElementById(id + '.10')
                let te = Math.max(Number(tenth.style.height.slice(0, -2)), tenth.scrollHeight)
                let max = Math.max(f, s, t, fo, fi, si, se, ei, ni, te)
                if(event != undefined){
                    if(document.getElementById(event.target.id).scrollHeight >= max){
                        for(let i = 1; i < 11; i++) if(i != 6 && i != 7) document.getElementById(id + '.' + i).style.height = document.getElementById(event.target.id).scrollHeight + "px"
                    }
                }
                else{
                    for(let i = 1; i < 11; i++) if(i != 6 && i != 7) document.getElementById(id + '.' + i).style.height = 'auto'
                    for(let i = 1; i < 11; i++) if(i != 6 && i != 7) document.getElementById(id + '.' + i).style.height = max + "px"
                }
            }
            else if(this.step == 3){
                let first = document.getElementById(id + '.1')
                let f = Math.max(Number(first.style.height.slice(0, -2)), first.scrollHeight)
                let second = document.getElementById(id + '.2')
                let s = Math.max(Number(second.style.height.slice(0, -2)), second.scrollHeight)
                let third = document.getElementById(id + '.3')
                let t = Math.max(Number(third.style.height.slice(0, -2)), third.scrollHeight)
                let fourth = document.getElementById(id + '.4')
                let fo = Math.max(Number(fourth.style.height.slice(0, -2)), fourth.scrollHeight)
                let max = Math.max(f, s, t, fo)
                if(event != undefined){
                    if(document.getElementById(event.target.id).scrollHeight >= max){
                        for(let i = 1; i < 5; i++) document.getElementById(id + '.' + i).style.height = document.getElementById(event.target.id).scrollHeight + "px"
                    }
                }
                else{
                    for(let i = 1; i < 5; i++) document.getElementById(id + '.' + i).style.height = 'auto'
                    for(let i = 1; i < 5; i++) document.getElementById(id + '.' + i).style.height = max + "px"
                }

            }
        },
        update_step0(num){
            if(this.$store.getters.role == 'metodist' || this.role_for_admin == 'metodist'){
                if(num == 1) this.job = document.getElementById('1.1').value
                else this.company = document.getElementById('1.2').value
            }
            else if(this.$store.getters.role == 'company' || this.role_for_admin == 'company'){
                this.company = document.getElementById('1.1').value
                this.org_city = document.getElementById('1.3').value
                this.responsible_person = document.getElementById('1.4').value
                this.post = document.getElementById('1.5').value
                this.phone = document.getElementById('1.6').value
                this.card_email = document.getElementById('1.7').value
                let val_2 = document.getElementById('1.2').value
                if(val_2 != 'Другое') this.sphere = val_2
                else{
                    this.sphere = ''
                    this.show_other(true)
                }
            }
        },
        update_step1(item, num){
            this.step1[item][num - 1] = document.getElementById(item + '.' + num).value
        },
        update_step2(item, num, index){
            if(index){
                let val = document.getElementById(item + '.' + num + '.' + index)
                let re = '1234567890,'
                if(val.value.indexOf(',', 2) != -1){
                    document.getElementById(item + '.' + num + '.' + index).value = this.step2[this.dop_step][item][num - 1][index - 1]
                    return
                }
                for(let i = 0; i < val.value.length; i++){
                    if(re.lastIndexOf(val.value[i]) == -1){
                        document.getElementById(item + '.' + num + '.' + index).value = this.step2[this.dop_step][item][num - 1][index - 1]
                        return
                    }
                }
                val = val.value.split(',')
                if(val.length == 1){
                    this.step2[this.dop_step][item][num - 1][index - 1] = Number(val[0])
                }
                else if(val.length == 2){
                    this.step2[this.dop_step][item][num - 1][index - 1] = Number(val[0] + '.' + val[1])
                }
                document.getElementById(item + '.' + num + '.5').innerHTML = this.count_hours(item, num)
            }
            else this.step2[this.dop_step][item][num - 1] = document.getElementById(item + '.' + num).value
        },
        update_step3(item, num){
            let n = 0
            for(let i = 0; i < this.step2.length; i++){
                for(let j = 0; j < this.step2[i].length; j++){
                    if(n == item - 1){
                        this.step2[i][j][9 + num] = document.getElementById(item + '.' + num).value
                        return
                    }
                    n++
                }
            }
        },
        make_normal(mes){
            let message = ''
            let n = 0
            let shy = String.fromCharCode(173)
            for(let i = 0; i < mes.length; i++){
                message += mes[i]
                if(mes[i] != ' ') n += 1
                else n = 0
                if(n == 12){
                    message += shy
                    n = 0
                }
            }
            return message
        },
        usefullInfo(){
            if(this.step == 1){
                Swal.fire({
                    width: '100%',
                    html: '<div>Алгоритм формирования результатов обучения</div>' +
                    '<ol class="text-left">' +
                        '<li>Перенос навыка Н1 либо из корпоративного стандарта, либо из требований работодателя.</li>' +
                        '<li>Формулировка (либо перенос) трудовых операций: ТО 1.1., ТО 1.2., ТО 1.3., ТО 1.n., - реализующих навык Н1.</li>' +
                        '<li>Формулировка показателя оценки для каждой трудовой операции: ПО 1.1., ПО 1.2., ПО 1.3., ПО 1.n.</li>' +
                    '</ol>' +
                    '<div class="text-left">Дополнение: По аналогии, согласно выше указанному алгоритму, формулировка навыков: Н2, ...Нn, с соответствующими трудовыми операциями и показателями оценки.</div>',
                })
            }
            else if(this.step == 2){
                Swal.fire({
                    width: '100%',
                    html: '<div>Алгоритм формирования содержания образовательной программы</div>' +
                    '<ol class="text-left">' +
                        '<li>Формулировка названия профессионального модуля ПМ. 01 с результатом обучения: навык Н1 с трудовыми операциями ТО 1.1., ТО 1.2., ТО 1.3., ТО 1.n.</li>' +
                        '<li>Формулировка тем практических занятий: ПЗ 1.1., ПЗ 1.2., ПЗ 1.3., ПЗ 1.n,  - с практическими упражнениями для отработки сответствующих трудовых операций.</li>' +
                        '<li>Формирование контрольного задания (КОС) для выявления уровня освоения трудовой операции либо комплексного задания для выявления уровня освоения навыка Н1 в целом.</li>' +
                        '<li>Определение количества часов на отработку трудовой операции ТО в рамках практического занятия в отдельности и навыка Н1 в целом, т.е. количества часов на профессиональный модуль ПМ. 01, в зависимости от категории слушателей.</li>' +
                        '<li>Формулировка МТБ.</li>' +
                    '</ol>' +
                    '<div class="text-left">Дополнение: По аналогии, согласно выше указанному алгоритму, формирование содержания профессиональных модулей: ПМ. 02, ...ПМ. 0n.</div>',
                })
            }
        },
        count_hours(item, num){
            return this.step2[this.dop_step][item][num - 1][0] + this.step2[this.dop_step][item][num - 1][1] + this.step2[this.dop_step][item][num - 1][2] + this.step2[this.dop_step][item][num - 1][3]
        },
        show_other(val){
            if(val) this.other = true
            else this.other = false
        },
    }
}
</script>

