<template>
        <nav class="navbar navbar-expand-lg navbar-light  fixed-top navStyle">
            <router-link to="/" class="router-link">
                <a class="navbar-brand" ref="home" style="color: #f23333">KorpStandard</a>
            </router-link>
            <button @click="animate_toggler()" class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <div class="anim-bar-dark anim-2">
                    <span class="icon-bar"></span>
                    <span class="icon-bar"></span>
                    <span class="icon-bar"></span>
                </div>
            </button>
            <div class="collapse navbar-collapse" id="navbarSupportedContent" @click="animate_navbar()">
                <ul class="navbar-nav mr-auto">  <!-- v-if="showHeaderContent" -->
                    <li class="nav-item">
                        <router-link to="/" class="router-link" v-if="this.$store.getters.role != 'admin'">
                            <a class="nav-link">Мои карточки</a>
                        </router-link>
                    </li>
                    <li class="nav-item">
                        <router-link to="/add-card" class="router-link" v-if="this.$store.getters.role != 'admin'">
                            <a class="nav-link">Добавить карточку</a>
                        </router-link>
                    </li>
                    <li class="nav-item">
                        <router-link to="/" class="router-link" v-if="this.$store.getters.role == 'admin'">
                            <a class="nav-link">Все карточки</a>
                        </router-link>
                    </li>
                    <li class="nav-item">
                        <router-link to="/filter-card" class="router-link" v-if="this.$store.getters.role == 'admin'">
                            <a class="nav-link">Общая карточка с фильтрами</a>
                        </router-link>
                    </li>
                    <li class="nav-item">
                        <router-link to="/partners" class="router-link">
                            <a class="nav-link" style="">Наши партнеры</a>
                        </router-link>
                    </li>
                </ul>
                <img style="margin-left: auto; margin-right: auto;" src="./../assets/logos.png" alt="">
                <router-link to='/login' class="router-link login ml-auto" style="display:block;">
                    <a type="button" class="btn btn-rounded-blue btn-lg" ref="login">{{loginText}}</a>
                </router-link>
                <div class="dropdown person_menu" style="display:none">
                    <button class="btn btn-rounded-blue dropdown-toggle user" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{loginText}}
                    </button>
                    <div class="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuButton">
                        <router-link to="/profile" class="router-link">
                            <a class="dropdown-item">Сменить пароль</a>
                        </router-link>
                        <div v-if="role == 'admin'">
                            <router-link to="/admin" class="router-link">
                                <a class="dropdown-item">Добавить пользователя</a>
                            </router-link>
                        </div>
                        <div class="dropdown-divider"></div>
                        <button @click="exit()" class="dropdown-item exit personMenuDrop" href="#">Выйти</button>
                    </div>
                </div>
            </div>
        </nav>
</template>


<script>
import Swal from 'sweetalert2'
// import $ from 'jquery'
export default {
    name: 'Navbar',
    data(){
        return {
            loginText: 'Войти',
            role: '',
            // showHeaderContent: false,
        }
    },
    beforeMount(){
        let email = this.$store.getters.email
        let SessionID = this.$store.getters.SessionID
        if(email != ''){
            fetch(this.$store.state.serverIp+'/api/getInformation', {
                method: 'POST',
                body: JSON.stringify({email: email, sessionid: SessionID}),
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(data => {
                if(data.data == '310'){
                    document.cookie = "email=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                    document.cookie = "SessionID=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
                    window.location.reload()
                }
                if(this.$store.state.email != undefined){
                    this.loginText = this.$store.state.email
                    this.role = this.$store.state.role
                    document.querySelector('.login').style.display = 'none'
                    document.querySelector('.person_menu').style.display = 'block'
                }
            })
            .catch(err => {
                console.log(err)
                Swal.fire({
                    icon: 'error',
                    title: 'На сервере проходят технические работы, приносим свои извинения за доставленные неудобства',
                    allowOutsideClick: false,
                    allowEscapeKey: false,
                    allowEnterKey: false,
                })
            })
        } 
    },

    methods: {
      exit(){
        document.cookie = "email=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
        document.cookie = "SessionID=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
        document.cookie = "role=" + ";expires=Thu, 01 Jan 1970 00:00:01 GMT"
        // this.$router.push({ path: `/login` })
        window.location.reload()
        document.location.href = "/login"
      },
      animate_navbar(){
          if(event.target.classList.contains('useful') == false && event.target.classList.contains('user') == false){
                document.querySelector('.navbar-collapse').classList.remove('show')
                document.querySelector('.navbar-toggler').classList.remove('active')
          }
      },
      animate_toggler(){
          document.querySelector('.navbar-toggler').classList.toggle('active')
      },
    }

}
</script>

<style scoped>
.router-link {
  text-decoration: none; /* отменяем подчеркивание ссылки */
  color: #fff;
}
.nav-link{
    color: #000 !important;
}
.dropdown-item{
    color: #16181b !important;
}
.exit{
  color: red;
}

.dropdown-item:active {
  background: #f1f2f3 !important;
  outline: 0px;
    -ms-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
}
.navStyle{
    background-color: rgba(255,255,255,1);
    box-shadow: 0px 1px 3px rgba(0,0,0,0.10);
}
</style>