import Vue from 'vue'
import VueRouter from 'vue-router'
import Vuex from 'vuex';
import { APP_STORE } from '../store';
import App from './App.vue'

import Login from './components/Login.vue'
import AddCard from './components/AddCard.vue'
import YourCards from './components/YourCards.vue'
import EditCard from './components/EditCard.vue'
import FilterCard from './components/FilterCard.vue'
import EditFilterCard from './components/EditFilterCard.vue'
import Admin from './components/Admin.vue'
import Profile from './components/Profile.vue'
import Partners from './components/Partners.vue'

Vue.config.productionTip = false
Vue.use(VueRouter)
Vue.use(Vuex)

const routes = [
  { path: '/login', component: Login },
  { path: '/add-card', component: AddCard },
  { path: '/filter-card', component: FilterCard },
  { path: '/', component: YourCards },
  { path: '/edit-card/:id', component: EditCard },
  { path: '/edit-filter-card/:id', component: EditFilterCard },
  { path: '/admin', component: Admin },
  { path: '/profile', component: Profile },
  { path: '/partners', component: Partners },
]

const router = new VueRouter({
  mode: 'history',
  routes, // сокращённая запись для `routes: routes`
})

const store = new Vuex.Store(APP_STORE);

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
